<template>
    <div class="box">
        <!-- <div class="header">
            <span>ele.me</span>
            <span><router-link to="/login">登录|注册</router-link></span>
        </div> -->
        <Header :ant="true" :gnt="false" :cot="false" :cat="true"></Header>
        <div class="loding" v-if="name">
            <img src=".././page/images/4.gif" alt />
        </div>
        <div class="content">
           <div class="location">
               <div>
                   <ul>
                       <li>当前定位城市：</li>
                       <li>定位不准时，请在城市列表里选择</li>
                   </ul>
               </div><hr/>
               <div  @click="setParams(arr.id)">{{arr.name}}</div>
           </div>
           <div class="item">
               <div class="pai">热门城市</div>
               <div class="city">
                   <div v-for="(item,index) in list" :key="index" @click="setParams(item.id)">
                       {{item.name}}
                   </div>
               </div>
           </div>
           <div class="item" v-for="(item,index) in cher" :key="index"  >
               <div class="pai">{{item}}</div>
               <div class="city">
                   <div v-for="(i,ind) in info[item]" :key="ind" @click="setParams(i.id)">
                       {{i.name}}
                    </div>
               </div>
           </div>
        </div>
    </div>
</template>

<script>
import Header from '../page/header/Header'
export default {
            data () {
                return {
                    arr:[],
                    list:[],
                    info:[],
                    cher:[],
                    name: true,
                }
            },
            components: {
                Header
            },
            mounted() {                  
                this.http.get("http://elm.cangdu.org/v1/cities?type=guess").then((res)=>{
                    // console.log(res.data)
                    this.arr=res.data
                })
                this.http.get("http://elm.cangdu.org/v1/cities?type=hot").then((res)=>{
                    // console.log(res.data)
                    this.list=res.data
                })
                this.http.get("http://elm.cangdu.org/v1/cities?type=group").then((res)=>{
                    // console.log(res.data)
                    this.info=res.data;
                    this.cher=Object.keys(res.data).sort()
                    setTimeout(()=>{
                        this.name=false
                    },1000)
                })
            },
            methods: {
                setParams(id){
                    this.$router.push({path:`/search/${id}`})
                },
            },

    }
    
</script>

<style lang="scss" scoped>
.loding {
  width: 100%;
  height: 100%;
  img {
    width: 8rem;
    padding-top: 2rem;
  }
}
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        // font-size: 0.35rem;
    }
    .header{
        width: 100%;
        height: 1rem;
        background: rgb(192, 144, 236);
        line-height: 1rem;
        color: white;
        display: flex;
        div{
            flex: 1;
        }
        div:nth-of-type(1){
            padding-left: 0.3rem;
        }
        div:nth-of-type(2){
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin-left: 1.3rem;
            a{
                color: white;
            }
        }
        div:nth-of-type(3){
            margin-left: 1.5rem;
        }
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        font-size: 0.35rem;
    }
    .location{
        width:100%;
        height: 2.2rem;
        border-bottom: 0.2rem solid rgb(248, 246, 244);
        div:nth-of-type(1){
            height: 1rem;
            line-height: 1rem;
            
            ul{
                font-size: 0.26rem;
                display: flex;
                li{
                    flex: 1;
                    margin: 0 0.25rem;
                }
                li:nth-of-type(2){
                    flex: 2;
                    margin-left: 1rem;
                }
            }
        }
        div:nth-of-type(2){
            height: 1rem;
            line-height: 1rem;
            margin-left: 0.3rem;
            color: rgb(188, 153, 221);
        }
    }
    .pai{
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        color: rgb(179, 154, 154);
        font-size: 0.25rem;
        padding-left: 0.3rem;
    }
    .city{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        div{
            width: 25%;
            height: 1rem;
            line-height: 1rem;
            text-align: center;
            border: 0.001rem solid rgb(230, 220, 220);
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            color: rgb(160, 121, 121);
            font-size: 0.25rem;
            a{
                text-decoration: none;
                color: rgb(160, 121, 121);
            }
        }
    }
</style>


